<template>
  <div>
  <vx-card title="Ressources Humaines">
      <vs-button icon-pack="feather" icon="icon-plus" @click="active = !active">Ajouter</vs-button>
    <vs-table v-model="humanResourcesData" pagination max-items="10" search :data="humanResources">
      <template slot="thead">
        <vs-th >Nom</vs-th>
        <vs-th >Prénom</vs-th>
        <vs-th >CIN</vs-th>
        <vs-th >Statut</vs-th>
        <vs-th >Adresse</vs-th>
        <vs-th >Téléphone</vs-th>
        <vs-th >Salaire</vs-th>
        <vs-th >Actions</vs-th>

      </template>

      <template slot-scope="{data}">
        <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">

          <vs-td :data="data[indextr].firstName">
            {{data[indextr].firstName}}
          </vs-td>

          <vs-td :data="data[indextr].lastName">
            {{data[indextr].lastName}}
          </vs-td>

          <vs-td :data="data[indextr].cin">
            {{data[indextr].cin}}
          </vs-td>

          <vs-td :data="data[indextr].status">
            {{data[indextr].status}}
          </vs-td>

          <vs-td :data="data[indextr].adr">
            {{data[indextr].adr}}
          </vs-td>

          <vs-td :data="data[indextr].telephone">
            {{data[indextr].telephone}}
          </vs-td>

          <vs-td :data="data[indextr].salary">
            {{data[indextr].salary}}
          </vs-td>
          <vs-td class="whitespace-no-wrap">
            <feather-icon icon="EditIcon" svgClasses="w-5 h-5 hover:text-primary stroke-current" @click="active = true" />
            <feather-icon icon="TrashIcon" svgClasses="w-5 h-5 hover:text-danger stroke-current" @click="DeleteRh(data[indextr])" class="ml-2"
             />
          </vs-td>

        </vs-tr>
      </template>
    </vs-table>

  </vx-card>

    <div class="mt-5">

      <div id="parentx-demo-5">
        <vs-sidebar position-right parent="body" default-index="1" color="primary" class="sidebarx sidebarpage"
          v-model="active">
          <div style="margin-top: 20%; padding:10%">
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" label="Nom" v-model="humanResourcesData.firstName" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" label="Prénom" v-model="humanResourcesData.lastName" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" type="number" label="CIN" v-model="humanResourcesData.cin" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full"  label="Adresse" v-model="humanResourcesData.adr" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" label="Filialle" v-model="humanResourcesData.subsidiary" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" label="Téléphone" v-model="humanResourcesData.telephone" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <vs-input class="w-full" type="number" label="Salaire" v-model="humanResourcesData.salary" />
              </div>
            </div>
            <div class="vx-row mb-2">
              <div class="vx-col w-full">
                <span>Statut</span>
                <v-select v-model="humanResourcesData.status" :options="['Employé']" class="mb-3">
                </v-select>
              </div>
            </div>
            <div class="vx-row">
              <div class="vx-col w-full">
                <vs-button class="mb-2" @click="Valid">Valider</vs-button>
                <vs-button color="warning" type="border" class="mb-2"
                @click="active=!active" >Annuler</vs-button>
              </div>
            </div>
        </div>
        </vs-sidebar>
      </div>
    </div>
  </div>
</template>

<script>
import Prism from 'vue-prism-component'
import axios from 'axios'
import vSelect from 'vue-select'

export default {
  data() {
    return {
      active: false,
      humanResourcesData: {
        id: '',
        firstName: '',
        lastName: '',
        status: '',
        adr: '',
        telephone: '',
        salary: '',
        cin: '',
        subsidiary: ''
      },
      humanResources: []
    }
  },
  components: {
    vSelect,
    Prism
  },
  mounted() {
    this.GetRh()
  },
  methods: {
    Valid() {
      if (this.humanResourcesData.id) {
        this.PutRh()
      } else {
        this.AddRh()
      }
    },
    async GetRh() {
      await axios
        .get("/humanresources/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.humanResources = res.data;
        });
    },
    async PutRh() {
      await axios
        .put("/humanresources/" + this.humanResourcesData.id + "/", this.humanResourcesData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Modification avec succès",
            color: "success",
          });
          this.active = false
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
        })
    },
    async AddRh() {
      await axios
        .post("/humanresources/", this.humanResourcesData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Ajout avec succès",
            color: "success",
          });
          this.GetRh()
          this.active = false
        })
        .catch(() =>{
          this.$vs.notify({
            title: "Erreur d'ajout",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
        })
    },
    DeleteRh(data) {
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${data.firstName} ${data.lastName}"`,
        acceptText: "Supprimer",
        accept: this.ConfirmDeleteRh,
        cancelText: "Annuler",

      });
    },
    async ConfirmDeleteRh() {
      await axios
        .delete("/humanresources/" + this.humanResourcesData.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Suppression avec succès",
            color: "success",
          });
          this.GetRh()
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de Suppression",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
        })
    },
  },
}
</script>
